







































import Vue from 'vue'
import { getTags } from '../api'

export default Vue.extend({
  data: (): { tags: string[] } => ({
    tags: []
  }),
  created() {
    getTags().then(tags => this.tags = tags).catch(e => console.log(e))
  }
})



