













































































import Vue from 'vue'
import headerBar from './components/headerBar.vue'
import { authorizationCheck } from './api'
import footerBar from './components/footerBar.vue'

export default Vue.extend({
  name: "App",
  components: {
    headerBar,
    footerBar
  },
  data: () => ({

  }),
  created() {
    // 初始化登录状态
    authorizationCheck()
    .then(res => res.data)
    .then(({result}) => this.$$app.logedin = result)
  }
})



